import { FilesIcon } from "@rp/react/common/assets"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, CircularProgress, Typography } from "@rp/react/web/mui/core"

const DropFileImage = ({ isLoading = false }) => {
  const { isMobile } = useDeviceInfo()
  if (isMobile) {
    return (
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          px: 2,
          height: "100%",
        }}
      >
        {isLoading ? (
          <CircularProgress />
        ) : (
          <Box sx={{ textAlign: "center" }}>
            <FilesIcon />
            <Typography sx={{ mt: 4, fontSize: 20, fontWeight: 700 }}>
              You haven't uploaded any files yet.
            </Typography>
            <Typography sx={{ color: "text.disabled" }}>
              Add your first file by clicking on the Upload File button.
            </Typography>
          </Box>
        )}
      </Box>
    )
  }

  return (
    <Box sx={{ display: "flex", alignItems: "center", width: 1, height: 1 }}>
      <Box sx={{ textAlign: "center", width: 1 }}>
        {isLoading ? (
          <CircularProgress />
        ) : (
          <>
            <FilesIcon />
            <Typography sx={{ mt: 2 }} variant="h6">
              You haven't uploaded any files yet.
            </Typography>
            <Typography style={{ color: "gray" }} variant="subtitle1">
              Add your first file by clicking on the
              <br />
              Upload File button.
            </Typography>
          </>
        )}
      </Box>
    </Box>
  )
}

export { DropFileImage }
